import styled from 'styled-components';

const QnAContainer = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 70px 0px;
	border-top: 8px solid rgb(25, 25, 25);
	width: 100%;
	@media (max-width: 768px) {
		padding: 30px 15px;
	}
`;

const QnAContent = styled.div`
	width: 800px;
	@media (max-width: 768px) {
		width: 100%;
	}
`;

const QnATitle = styled.h1`
	color: white;
	font-size: 42px;
	text-align: center;
	@media (max-width: 768px) {
		font-size: 27px;
	}
`;

const QnASection = styled.div`
	margin-top: 65px;
	@media (max-width: 768px) {
		margin-top: 30px;
	}
`;

const QnASubSection = styled.div``;

const QnASubTitle = styled.div`
	color: white;
	background-color: #303030;
	padding: 28px 0px;
	padding-left: 20px;
	padding-right: 20px;
	font-size: 24px;
	position: relative;
	cursor: pointer;
	margin-bottom: 10px;
	@media (max-width: 768px) {
		padding: 20px 15px;
		font-size: 16px;
	}
`;

const QnASubDesc = styled.div`
	color: white;
	background-color: #303030;
	font-size: 21px;
	line-height: 1.6;
	border-top: 1px solid black;
	position: absolute;
	top: 0;
	left: 0;
	height: 0px;
	overflow: hidden;
	box-sizing: border-box;
	z-index: -1;
	padding-top: 30px;
	&.active {
		position: static;
		height: 240px;
		margin-top: 25px;
		transition: 0.3s;
	}
	@media (max-width: 768px) {
		margin-top: 20px;
		padding: 0;
		padding-top: 20px;
		font-size: 15px;
		&.active {
			height: 240px;
		}
	}
`;

const handleQnATitle = (event: any) => {
	event.target.children[0].classList.toggle('active');
};

const QnA = () => {
	return (
		<QnAContainer>
			<QnAContent>
				<QnATitle>  </QnATitle>
				<QnASection>
					<QnASubSection>
						<QnASubTitle onClick={handleQnATitle}>
							 ?
							<QnASubDesc>
								     TV , ,
								,       
								       .
								<br />
								<br />
								         
								  .     
								 TV   .
							</QnASubDesc>
						</QnASubTitle>
						<QnASubTitle onClick={handleQnATitle}>
							  ?
							<QnASubDesc>
								, ,  TV, ,    
								     .
								<br />
								<br />
								   9,500 14,500 .
								<br />
								   .
							</QnASubDesc>
						</QnASubTitle>
						<QnASubTitle onClick={handleQnATitle}>
							   ?
							<QnASubDesc>
								      .
								<br />
								<br />
								   PC netflix.com  
								  ,      
								( TV, , ,   ,
								  )    .
							</QnASubDesc>
						</QnASubTitle>
						<QnASubTitle onClick={handleQnATitle}>
							   ?
							<QnASubDesc>
								   .  , 
								.
								<br />
								        .
								<br />
								<br />
								       
								.
							</QnASubDesc>
						</QnASubTitle>
						<QnASubTitle onClick={handleQnATitle}>
							     ?
							<QnASubDesc>
								  , , TV , , 
								       
								.
								<br />
								<br />
								       .
							</QnASubDesc>
						</QnASubTitle>
					</QnASubSection>
				</QnASection>
			</QnAContent>
		</QnAContainer>
	);
};

export default QnA;
